import React from 'react';
import engPdf from '../images/Lianel-Artiles-Dev-CV-Eng.pdf';
import espPdf from '../images/Lianel-Artiles-Dev-CV-Esp.pdf';

const Pdf = ({ language }) => {
  return (
    <div style={{ position: 'absolute', width: '100%', height: '100%' }}>
      <object
        data={language.esp ? espPdf : engPdf}
        type="application/pdf"
        width="100%"
        height="100%"
      >
      </object>
    </div>
  );
}

export default Pdf;